:root {
  --black: #333;
  --byline: #999;
  --white: #fff;
  --gray: #edeef0;
  --blue: #18a0fb;
  --green: #1bc47d;
  --yellow: #f2bd00;
  --orange: #f97f0f;
  --red: #f24822;
  --purple: #7b61ff;
  --teal: #00b5ce;
  --pink: #ee46d3;
  --shadow-color-nebula: #b664b0;
  --shadow-melon: #00B3CC;
  --largePadding: 88px;
  --mediumPadding: 64px;
  --smallPadding: 40px;
  --verysmallPadding: 20px;
  --melon: linear-gradient(135deg, #D6FF7F 0%, #00B3CC 100%);
  --blackberry: linear-gradient(135deg, #CB5EEE 0%, #4BE1EC 100%);
  --grapefruit: linear-gradient(135deg, #FFCC33 0%, #E233FF 100%);
  --newc: linear-gradient(135deg, #6699FF 0%, #FF3366 100%);
  --newc2: linear-gradient(135deg, #ecc64b 0%, #4bec56 100%); 
}

.App {
  text-align: center;
  background: var(--white);
  height: 100vh;
}

.welcome-text {
  background: var(--blackberry); 
  color:#9D8EEE;
  background-clip:text;
  font-size: 36px;
  font-weight: bolder;
  -webkit-text-fill-color:transparent;
}

.card {
-webkit-font-smoothing: antialiased;
color: var(--black);
margin: 0;
list-style: none;
text-decoration: none;
box-sizing: border-box;
width: 100%;
border-radius: 40px;
border: var(--largePadding);
padding: var(--smallPadding);
min-width: 100px;
min-height: 80px;
display: flex;
flex-direction: column;
justify-content: space-between;
}

.restrained {
  max-height: 200px;
}

.card2 {
  -webkit-font-smoothing: antialiased;
  color: var(--black);
  margin: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
  width: 100%;
  border-radius: 40px;
  border: var(--smallPadding);
  padding: var(--verysmallPadding);
  min-height: 80px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  }

.melon {
  background: var(--melon);
  background-color: var(--melon);
}

.blackberry {
  background: var(--blackberry);
  background-color: var(--blackberry);
}

.grapefruit {
  background: var(--grapefruit);
  background: var(--grapefruit);
}

.newc {
  background: var(--newc);
  background: var(--newc);
}

.section {
  -webkit-font-smoothing: antialiased;
  text-shadow: 0 2px 4px rgba(0,0,0,.08);
  padding: 10px;
  margin: 10px;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
  border-radius: 48px;
  display: inline-block;
  border: none;
  color: var(--white);
  background-color: var(--gray);
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}

.clear {
  background-color: rgba(0,0,0,.12)
}

.back {
  font-style: bolder;
  color: black;
  font-size: 20px;
}

.paragraph {
  -webkit-font-smoothing: antialiased;
  display: flex;
  text-align: left;
  flex-direction: column;
  text-shadow: 0 2px 4px rgba(0,0,0,.08);
  justify-content: center;
  justify-items: center;
  max-width: 650px;
  padding: 10px;
  margin: 10px;
  border-radius: 48px;
  display: inline-block;
  border: none;
  color: var(--black);
  background-color: var(--gray);

}




.italic {
  font-style: italic;
}

.section2 {
  box-shadow: 0 10px 20px -10px var(--shadow-color-nebula);
  margin-bottom: 43px;
}

.Contactimg {
  border-radius: 50%;
  min-width: 100px;
  min-height: 100px;
  max-width: 200px;
  max-width: 200px;
}

.logo {
  width: 40px;
}

.workimg {
  max-width: 300px;
  min-width: 50px;

  max-height: 300px;
  min-height: 50px;
  display: inline;
  float: none;
  vertical-align: top;

  /* max-width: 60vw;
  max-height: 80%; */
  object-fit: contain;
}